
.UIButton {
  width: 3rem;
  height: 3rem;
  position: absolute;
  z-index: 3;
  top: 0rem;
  left: 0rem;

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;

  cursor: pointer;
  background-color: #0999ff;
  border-radius: 4px;
  -webkit-transition: -webkit-box-shadow 0.2s;
  transition: -webkit-box-shadow 0.2s;
  transition: box-shadow 0.2s;
  transition: box-shadow 0.2s, -webkit-box-shadow 0.2s;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  pointer-events: auto;
}
.UIButton>p {
  margin-top: auto;
  margin-bottom: auto;
}



