
.UIImage {
  width: 3rem;
  height: 3rem;
  position: absolute;
  z-index: 3;
  top: 0rem;
  left: 0rem;

  -webkit-transition: -webkit-box-shadow 0.2s;

  transition: -webkit-box-shadow 0.2s;

  transition: box-shadow 0.2s;

  transition: box-shadow 0.2s, -webkit-box-shadow 0.2s;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  pointer-events: none;
}
.UIImage.active {
  -webkit-box-shadow: 0 0 7px 2px #0999ff;
          box-shadow: 0 0 7px 2px #0999ff;
}
.UIImage>img {
  width: 100%;
  height: 100%;
  pointer-events: auto;
}



