
.UIText {
  position: absolute;
  z-index: 3;

  -webkit-transition: -webkit-box-shadow 0.3s;

  transition: -webkit-box-shadow 0.3s;

  transition: box-shadow 0.3s;

  transition: box-shadow 0.3s, -webkit-box-shadow 0.3s;

  pointer-events: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.UIText.active {
  -webkit-box-shadow: 0 0 7px 2px #0999ff;
          box-shadow: 0 0 7px 2px #0999ff;
}
.UIText>div {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.UIText>div>p {
  width: 100%;
  height: 100%;
  margin: 0px;
}
.UIText>div>textarea {
  width: 100%;
  height: auto;
  background-color: rgba(0,0,0,0);
  resize: none;
  border: none;
  margin: auto;
}
.UIText>div>textarea:focus {
  outline: none;
}



