

.normalBtn {
	width: 5.71rem;
	height: 2rem;
	display: inline-flex;
	justify-content: center;
	background: #0993FF;
	border-radius: 4px;

	color: white;
	text-align: center;
	line-height: 2rem;

	margin-top: auto;
	margin-bottom: auto;

	cursor: pointer;

	user-select: none;
}

.normalBtn:hover,
.normalBtn:active{
	background: #3177B2;
}

.scrollBar {
	background: #272930;;
	border-radius: 58px;
	height: 0.29rem;
	width: 10rem;
	position: relative;
	margin-bottom: auto;
	margin-top: auto;
	display: flex;
}

.scrollBar>span {
	position: absolute;
	z-index: 1;
	border-radius: 58px;
	height: 0.29rem;
	width: 100%;
	background-color: #0999FF;
}

input[disabled] {
	color: #7B7C7F;
	cursor: not-allowed;
}







