
.UIButton_SP {
  width: auto;
  height: 5.7rem;

  margin-top: 0.71rem;
  margin-bottom: auto;

  margin-left: 1rem;
  margin-right: 1rem;

  min-width: 5.7rem;
  min-height: 5.7rem;

  position: relative;
  pointer-events: auto;
}
.UIButton_SP>div {
  width: auto;
  height: 100%;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  /*justify-content: center;*/

  position: relative;

  min-width: 5.7rem;

  -webkit-user-select: none;

     -moz-user-select: none;

      -ms-user-select: none;

          user-select: none;
  cursor: pointer;
}
.UIButton_SP>div>span {
  position: absolute;
  z-index: 0;
  width: 5.7rem;
  height: 5.7rem;
  background-size: 5.7rem 5.7rem;
  background-repeat: no-repeat;
}
.UIButton_SP>div>span[role=texDef] {
  left: calc(50% - 2.85rem);
}
.UIButton_SP>div>span[role=texAct] {
  z-index: 1;
  left: calc(50% - 2.85rem);
}
.UIButton_SP>div>span[role=back] {
  position: absolute;
  z-index: 0;

  top: 0;
  left: calc(50% - 2.85rem);

  width: 5.7rem;
  height: 5.7rem;
}
.UIButton_SP>div>span[role=front] {
  z-index: 1;

  width: calc(5.4rem);
  height: calc(5.4rem);
  left: calc(50% - 2.85rem);

  /*top: 0.15rem;
  left: 0.15rem;*/
  -webkit-transform: translate(0.14rem, 0.14rem);
          transform: translate(0.14rem, 0.14rem);

  -webkit-transition: background-color 0.3s;

  transition: background-color 0.3s;
}
.UIButton_SP>span[role=deleteBtn] {
  position: absolute;
  z-index: 2;
  width: 25%;
  height: 25%;
  background-color: rgba(255,255,255,0.50);
  border-radius: 50%;
  top: -12.5%;
  right: -14.5%;

  display: -webkit-inline-box;

  display: -ms-inline-flexbox;

  display: inline-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  color: #ffffff;

  -webkit-user-select: none;

     -moz-user-select: none;

      -ms-user-select: none;

          user-select: none;
  cursor: pointer;
}
.UIButton_SP>div>p {
  z-index: 2;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-color: transparent;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  text-align: center;
  word-wrap: break-word;
  margin: auto;
}
.UIButton_SP[role=circle]>div>span[role=front],
.UIButton_SP[role=circle]>div>span[role=back] {
  -webkit-clip-path: circle(50% at 50% 50%);
          clip-path: circle(50% at 50% 50%);
}
.UIButton_SP[role=square]>span {
}
.UIButton_SP[role=square_r]>div>span[role=front],
.UIButton_SP[role=square_r]>div>span[role=back] {
  border-radius: 0.32rem;
}
.UIButton_SP[role=square_rx]>div>span[role=front],
.UIButton_SP[role=square_rx]>div>span[role=back] {
  border-radius: 0.71rem;
}
.UIButton_SP[role=rhombus]>div>span[role=front],
.UIButton_SP[role=rhombus]>div>span[role=back] {
  -webkit-clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
          clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
}
.UIButton_SP[role=hexagon]>div>span[role=back],
.UIButton_SP[role=hexagon]>div>span[role=front] {
  -webkit-clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
          clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
}




