
@import './assets/css/framework.css';
@import './assets/css/main.css';
@import './assets/css/navigation.css';
@import './assets/css/loading.css';
.infoBoard {
  position: absolute;
  z-index: 4000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: #ffffff;

  -webkit-transition: opacity 0.3s;

  transition: opacity 0.3s;
  -webkit-transition-delay: 2s;
          transition-delay: 2s;
  pointer-events: none;
}
.infoBoard>p {
  font-size: 2rem;
  margin: auto;
  color: red;
}
.testDIV {
  width: 45.72rem;
  height: 10rem;
  background-color: #0999ff;
}
.debugInfo {
  width: 50%;
  height: 50%;
  background-color: rgba(0,0,0, 0.35);
  pointer-events: none;
  overflow: none;

  position: absolute;
  z-index: 99999;
  color: white;

  resize: none;
}



