html {
	font-size: 14px;
}

body {
	width: 100%;
	height: 100%;
	margin: auto;
	background-color: black;
}

#app {
	width: 100%;
	height: 100%;
	display: flex;
	position: relative;
	margin: auto;
	background-color: white;
	overflow: hidden;
}

p {
	font-size: 1rem;
	color: #FFFFFF;
}

* {
	font-family:'San Francisco', Helvetica, 'Droid Sans', 'PingFangSC-Light', 'HiraginoSans-W3', 'Heiti SC', 'noto sans', 'Microsoft YaHei', sans-serif;
	-webkit-font-smoothing: antialiased;
}

