#glCanvas {
	background-color: rgba(0,0,0,0.35);
}

.window {
	width: 100%;
	height: 100%;
}

.window>div {
	width: 100%;
	height: 100%;
}

.window>div>canvas {
	width: 100vw;
	height: 100vh;
}

.window>div>.windowUIContainer {
	width: 100%;
	height: 100%;
	position: absolute;
	z-index: 1;
	top: 0;
	left: 0;

	pointer-events: none;
}

.window>div>.windowUIContainer>* {
	pointer-events: auto;
}

::-webkit-scrollbar {
	width: 0.7rem;
	height: 0.7rem;
	background-color: rgba(0,0,0,0.0);
}

 
::-webkit-scrollbar-thumb {
	background-color: rgba(0,0,0,0.2);
	border-radius: 2rem;
}

::-webkit-scrollbar-thumb:hover {
	background-color: rgba(0,0,0,0.35);
}

::-webkit-scrollbar-corner {
	background-color: rgba(0,0,0,0);
}

input[type=text] {
	background: #272930;
	border: 0.07rem solid #595C66;
	border-radius: 4px;
	text-indent: 0.71rem;

	color: white;
}

div,span {
	user-select: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
}


