
.window>div {
  position: relative;
  background-color: #313237;
}
.musicIcon {
  width: 3rem;
  height: 3rem;
  position:  absolute;
  z-index: 3501;
  right: 0.5rem;
  top: 0.5rem;
}
.musicIcon>img {
  width: 100%;
  cursor: pointer;
}
.musicIcon>img[role=playing] {
  -webkit-animation: musicRotate 1s 0s cubic-bezier(.09, .57, .49, .9) infinite;
          animation: musicRotate 1s 0s cubic-bezier(.09, .57, .49, .9) infinite;
}
@-webkit-keyframes musicRotate {
0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);
}
30% {-webkit-transform: rotate(-18deg);transform: rotate(-18deg);
}
90% {-webkit-transform: rotate(9deg);transform: rotate(9deg);
}
100% {-webkit-transform: rotate(0deg);transform: rotate(0deg);
}
}
@keyframes musicRotate {
0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);
}
30% {-webkit-transform: rotate(-18deg);transform: rotate(-18deg);
}
90% {-webkit-transform: rotate(9deg);transform: rotate(9deg);
}
100% {-webkit-transform: rotate(0deg);transform: rotate(0deg);
}
}



