
.pageContainer {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
}
.commonUI {
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}
.pageBkgImg {
  position: absolute;
  z-index: -10;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}



