.navigation {
  position: absolute;
  z-index: 0;

  width: 100%;
  height: 7.15rem;
  background-color: transparent;
  left: 0;
  bottom: 0;

  display: flex;
  justify-content: center;
  pointer-events: none;
}

.navigation>div[role=left],
.navigation>div[role=right] {
  position: absolute;
  bottom: 0px;
  width: 25%;
  height: 200%;
  display: flex;
  pointer-events: auto;
}

.navigation>div[role=left] {
  left: 0px;
}

.navigation>div[role=right] {
  flex-direction: row-reverse;
  right: 0px;
}

.navigation>div[role=left]>span,
.navigation>div[role=right]>span {
  width: 3.57rem;
  height: 3.57rem;
  pointer-events: auto;
  background-size: 3.57rem 3.57rem;
  position: absolute;
  bottom: calc(25% - 1.8rem);
}

.navigation>div[role=left]>span {
  background-image: url(../tex/common/lastPage_nav.png);
  left: 1.79rem;
}

.navigation>div[role=right]>span {
  background-image: url('../tex/common/nextPage_nav.png');
  right: 1.79rem;
}

.navigation>div[role=thumbnail] {
  width: 15rem;
  height: 30%;
  background-color: transparent;
  margin-top: auto;
  margin-bottom: auto;

  display: flex;
  justify-content: center;
}

.navigation>div[role=thumbnail]>span {
  width: 0.71rem;
  height: 0.71rem;
  border-radius: 50%;
  margin: auto;

  background-color: rgb(135,135,135);

  transition: box-shadow 0.2s;
  box-shadow: 0 0 7px;
}

.navigation>div[role=thumbnail]>span.active {
  background-color: rgba(255,255,255, 1);
  box-shadow: 0 0 8px 2px #676767;
}


