/*loading1*/
.UILoading_1 {
  display: flex;
}

.UILoading_1>div {
  display: block;
}

.UILoading_1 > div:nth-child(1), .UILoading_1 > div:nth-child(5) {
  animation-delay: -0.4s !important;
}

.UILoading_1 > div:nth-child(2), .UILoading_1 > div:nth-child(4) {
    animation-delay: -0.2s !important;
}

.UILoading_1>div {
  background-color: #fff;
  width: 10%;
  height: 100%;
  border-radius: 0.142rem;
  margin: auto;
  animation-fill-mode: both;
  display: inline-block;
  animation: line-scale-pulse-out 0.9s 0s infinite cubic-bezier(.85, .25, .37, .85);
}

@keyframes line-scale-pulse-out {
0% {transform: scaley(1);}
50% {transform: scaley(0.4);}
100% {transform: scaley(1);}
}

/*//////////////////////////////////////////*/
/*//////////////////////////////////////////*/

/*loading2*/
.UILoading_2 {
  display: flex;
}

.UILoading_2>div {
  background-color: #fff;
  width: 10%;
  height: 0;
  padding-bottom: 10%;
  border-radius: 100%;
  margin: auto;
  animation-fill-mode: both;
  display: inline-block;
}

.UILoading_2 > div:nth-child(1) {
  animation: scale 0.75s -0.63s infinite cubic-bezier(.2, .68, .18, 1.08);
}

.UILoading_2 > div:nth-child(2) {
    animation: scale 0.75s -0.51s infinite cubic-bezier(.2, .68, .18, 1.08);
}

.UILoading_2 > div:nth-child(3) {
    animation: scale 0.75s -0.39s infinite cubic-bezier(.2, .68, .18, 1.08);
}

@keyframes scale {
30% {transform: scale(0.3);}
100% {transform: scale(1);}
}

/*//////////////////////////////////////////*/
/*//////////////////////////////////////////*/

/*loading3*/
.UILoading_3 {
  position: relative;
  width: 100%;
  height: 100%;
}

.UILoading_3>div {
  position: absolute;
  top: 0rem;
  left: 0rem;
  border-radius: 100%;
  animation-fill-mode: both;
}
.UILoading_3>div:first-child {
  background: #fff;
  height: 80%;
  width: 80%;
  /*top: calc(10% + 0.14rem);*/
  /*left: calc(10% + 0.14rem);*/
  top: 10%;
  left: 10%;
  animation: scale 1s 0s cubic-bezier(.09, .57, .49, .9) infinite;
}

.UILoading_3>div:last-child {
  position: absolute;
  border: 0.14rem solid #fff;
  width: 100%;
  height: 100%;
  top: -0.14rem;
  left: -0.14rem;
  background: transparent;
  border-color: #fff transparent #fff transparent;

  animation: rotate 1s 0s cubic-bezier(.09, .57, .49, .9) infinite;

  animation-duration: 1s;
}

@keyframes scale {
  30% {transform: scale(0.3);}
  100% {transform: scale(1);}
}

@keyframes rotate {
  0% {transform: rotate(0deg) scale(1);}
  50% {transform: rotate(180deg) scale(0.6);}
  100% {transform: rotate(360deg) scale(1);}
}


